import Vue from 'vue'
import Router from 'vue-router'
import Top from '@/components/Top.vue'
import PcPage from '@/components/PcPage.vue'

Vue.use(Router)

const router = new Router({
  base: process.env.ROOT_BASE,
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  },
  routes: [
    {
      path: '/',
      name: 'Top',
      component: Top,
      meta:{isSp: true}
    },
    {
      path: '/sperror',
      name: 'PcPage',
      component: PcPage,
    },
  ]
})

router.beforeEach((to, from, next) => {
  // スマホ専用ページ
  console.log()
  if (to.matched.some(record => record.meta.isSp)) {
    var width = window.innerWidth;
    if(width < 430) {
      next();
    } else {
      next('/sperror')
    }
  } else {
    next();
  }
});

export default router


